import React from 'react'
import style from './Portfolio.module.css'
import styleContainer from "../common/styles/Container.module.css";
import { ExampleWork } from './exempleWork/ExampleWork';

export const Portfolio = () => {
    return (
        <div className={style.portfolio}>
            <div className={`${styleContainer.container} ${style.containerPortfolio}`}>
                <div className={style.portfolioTitle}>
                    <span>Portfolio</span>
                    <h3>SELECTED WORK</h3>
                </div>
                <div className={style.portfolioBlock}>
                    <ExampleWork name={"Counter"} description={"Increase/Reset value"}
                                 img = {"data:image/png;base64,"}/>
                    <ExampleWork name={"TodoList"} description={"Your tasks on day/week/year"}
                                 img = {"data:image/png;base64,"}/>
                </div>
            </div>
        </div>
    )
}